.project_track h1{
    color: black;
    font-size: 25px;
    text-align: center;
    font-weight: 500;
}
.project_track p{
    font-size: 15px;
    text-align: center;
    max-width: 1400px;
    color: black;
}
.project_set{
    display: flex;
    flex-direction: column;
    -webkit-box-pack: center;
    justify-content: center;
    position: relative;
    z-index: 1;
    -webkit-box-align: center;
    align-items: center;
    margin-top: 25px;
   

}
.all{
    margin-top: 15px;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    gap: 28px;
    flex-wrap: wrap;
}
.project1{
    width: 330px;
    height: 350px;
    background-color: rgb(1, 148, 128);
    cursor: pointer;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 0px 12px 4px;
    overflow: hidden;
    padding: 26px 20px;
    display: flex;
    flex-direction: column;
    gap: 14px;
    transition: all 0.5s ease-in-out 0s;
}
.Projec1{
    width: 100%;
    height: 50px;
    background-color:white;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 0px
}
.pro1{
    width: 100%;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 8px;
    margin-top: 5px;
}
.skills{
    font-size: 12px;
    font-weight: 400;
    color: rgb(10, 9, 10);
    background-color: rgba(133, 76, 230, 0.082);
    padding: 2px 8px;
    border-radius: 10px;
}
.tittle1{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0px;
    padding: 0px 2px;
    color: black;
}
.name{
    font-size: 20px;
    font-weight: 600;
    color: rgb(177, 178, 179);
    display: -webkit-box;
    max-width: 100%;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    color: black;
}
.date{
    font-size: 12px;
    margin-left: 2px;
    font-weight: 400;
    color: black;
    margin-top: 5px;
}
.info{
    font-weight: 400;
    color: rgba(177, 178, 179, 0.6);
    overflow: hidden;
    margin-top: 8px;
    display: -webkit-box;
    max-width:200%;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    color: black;
    margin-top: 5px;
}
.probreak{
        display: flex;
    -webkit-box-align: center;
    align-items: center;
    padding-left: 10px;
}